<template>
    <div class="scorecard">
        (X) You {{ points.user }} - {{ points.cpu }} CPU (O)
    </div>
    <game-board :single-player="true" @game-over="gameOver"></game-board>
</template>

<script>
import GameBoard from "../components/game/GameBoard.vue";

export default {
    components: {
        GameBoard,
    },
    data() {
        return {
            points: {
                user: 0,
                cpu: 0,
            },
        };
    },
    methods: {
        gameOver(winner) {
            if (winner == "X") {
                this.points.user++;
            } else if (winner == "O") {
                this.points.cpu++;
            } else {
                this.points.user += 0.5;
                this.points.cpu += 0.5;
            }
        },
    },
};
</script>

<style scoped>
.scorecard {
    font-size: 20px;
}
</style>
