<template>
    <div class="board">
        <div v-for="(row, rind) in board" :key="rind">
            <board-cell
                v-for="(col, cind) in row"
                :key="cind"
                @click="playTurn(rind, cind)"
            >
                {{ col }}
            </board-cell>
        </div>
    </div>
    <div class="status">
        {{ status }}
        <button v-if="!isRunning" type="button" @click="resetGame">
            Play Again
        </button>
        <button v-else type="button" @click="forfeit">Forfeit</button>
    </div>
</template>

<script>
import BoardCell from "./BoardCell.vue";

export default {
    props: ["single-player"],
    emits: ["game-over"],
    components: {
        BoardCell,
    },
    data() {
        return {
            isRunning: false,
            board: [],
            turn: "",
            winner: "",
            completedTurns: 0,
        };
    },
    watch: {
        isRunning(current) {
            if (current) return;
            this.$emit("game-over", this.winner);
        },
    },
    computed: {
        status() {
            if (!this.isRunning) {
                if (this.winner == "X" || this.winner == "O") {
                    return `"${this.winner}" won the game!`;
                }

                return "It's a draw!";
            }

            return `Current Turn: ${this.turn}`;
        },
    },
    methods: {
        playTurn(rind, cind) {
            if (!this.isRunning || this.board[rind][cind] != "") return;

            this.updateBoard(rind, cind);

            if (this.isRunning && this.singlePlayer) {
                this.playComputerTurn();
            }
        },
        playComputerTurn() {
            for (let i = 0; i < 3; i++) {
                for (let j = 0; j < 3; j++) {
                    if (this.board[i][j] == "") {
                        this.updateBoard(i, j);
                        return;
                    }
                }
            }
        },
        switchTurn() {
            this.turn = this.turn == "X" ? "O" : "X";
        },
        updateBoard(i, j) {
            this.board[i][j] = this.turn;
            this.completedTurns++;
            this.switchTurn();
            this.checkResult(i, j);
        },
        checkResult(i, j) {
            let rcount = 0;
            let ccount = 0;
            let d1count = 0;
            let d2count = 0;

            for (let k = 0; k < 3; k++) {
                if (this.board[i][k] == this.board[i][j]) rcount++;
                if (this.board[k][j] == this.board[i][j]) ccount++;
                if (i == j && this.board[k][k] == this.board[i][j]) d1count++;
                if (i + j == 2 && this.board[k][2 - k] == this.board[i][j])
                    d2count++;
            }

            if (rcount == 3 || ccount == 3 || d1count == 3 || d2count == 3) {
                this.isRunning = false;
                this.winner = this.board[i][j];
                return;
            }

            if (this.completedTurns == 9) {
                this.isRunning = false;
                this.winner = "D";
            }
        },
        resetGame() {
            this.isRunning = true;
            this.board = [];

            for (let i = 0; i < 3; i++) {
                const row = [];

                for (let j = 0; j < 3; j++) {
                    row.push("");
                }

                this.board.push(row);
            }

            this.turn = "X";
            this.winner = "";
            this.completedTurns = 0;
        },
        forfeit() {
            this.switchTurn();
            this.isRunning = false;
            this.winner = this.turn;
        },
    },
    created() {
        this.resetGame();
    },
};
</script>

<style scoped>
.board {
    margin-top: 20px;
    margin-bottom: 20px;
    width: 298px;
    height: 298px;
    display: grid;
    grid-template-columns: repeat(3, auto);
    border: 1px solid black;
}

.status > button {
    margin-left: 10px;
    height: 24px;
    cursor: pointer;
    background-color: lightgray;
    border: 1px solid black;
    border-radius: 2px;
}
</style>
